<template>
  <div class="login-container">
    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-form">
      <h3 class="title">欢迎登录问卷生成系统</h3>
      <el-form-item prop="account">
        <el-input type="text" prefix-icon="el-icon-user" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="checkPass">
        <el-input type="password" prefix-icon="el-icon-lock" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import api from '~/api/api';
  import store from '~/store/index';
  import * as types from '~/store/mutation-type';
  import {setStore} from '~/tools/utils';
  
  export default {
    data() {
      return {
        logining: false,
        ruleForm2: {
          account: '',
          checkPass: ''
        },
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {
      handleReset2() {
        this.$refs.ruleForm2.resetFields();
      },
      handleSubmit2(ev) {
        var _this = this;
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            //_this.$router.replace('/table');
            this.logining = true;
            //NProgress.start();
            var loginParams = { username: this.ruleForm2.account, password: this.ruleForm2.checkPass };
            
            api.requestLogin(this.ruleForm2.account, this.ruleForm2.checkPass).then(res => {
              this.logining = false;
              //NProgress.done();
              if (res) {
                setStore('name', res.data.realname);
                this.$router.push({ path: '/list' });
              }
            });
          } else {
            return false;
          }
        });
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .login-container {
    min-height: 100%;
    width: 100%;
    background-color: #EEEFF1;
    overflow: hidden;
    .login-form {
      -webkit-border-radius: 5px;
      border-radius: 5px;
      -moz-border-radius: 5px;
      background-clip: padding-box;
      margin: 180px auto;
      width: 350px;
      padding: 35px 35px 15px 35px;
      background: #fff;
      border: 1px solid #eaeaea;
      box-shadow: 0 0 25px #cac6c6;
      .el-input {
        input {
          padding-left: 40px;
        }
      }
      .title {
        margin: 0px auto 40px auto;
        text-align: center;
        color: #505458;
      }
      .remember {
        margin: 0px 0px 35px 0px;
      }
      .icon-container {
        padding: 6px 5px 6px 15px;
        color: #889aa4;
        vertical-align: middle;
        width: 30px;
        display: inline-block;
      }
    }
  }
</style>



